import { extension } from '@extension/common/webextension';
import { BuildSettings } from '@extension/common/constants';

export default class About
{
  static start(settingsData, bkGlobals, strConvertor)
  {
    if (!bkGlobals.nativeSettings.CRYPTOJACKING_DISABLED.value)
    {
      const cryptoLearnUrl = extension.runtime.getURL('html/mcafee_wa_crypto_learn.html');
      const cryptoLink = `<a href=${cryptoLearnUrl} target='_blank'>${strConvertor.localeData('settings_about_cryptojackers_link')}</a>`;
      const cryptLearnText = strConvertor.localeData('settings_about_cryptojackers', [cryptoLink]);
      const cryptoItem = $(`<li id="settings_about_cryptojackers">${cryptLearnText}</li>`);
      $('ul.section__message').append(cryptoItem);
    }

    // Populate Product version
    About.populateVersionDetails(bkGlobals, strConvertor);
  }

  static populateVersionDetails(bkGlobals, strConvertor)
  {
    const extensionVersionText = strConvertor.localeData('settings_about_product_extension_version', [BuildSettings.EXTENSION_VERSION.toString()]);
    $('#settings_about_product_extension_version').text(extensionVersionText);

    if (bkGlobals.nativeSettings.BINARY_VERSION.value !== null)
    {
      const binaryVersionText = strConvertor.localeData('settings_about_product_binary_version', [bkGlobals.nativeSettings.BINARY_VERSION.value.toString()]);
      $('#settings_about_product_binary_version').text(binaryVersionText);
    }

    const affidText = strConvertor.localeData('settings_about_product_affid', [bkGlobals.nativeSettings.AFF_ID.value.toString()]);
    $('#settings_about_product_affid').text(affidText);
  }
}
