// Common
import { LocalStorageVariables, CacheStoreActions } from '@extension/common/cache_store_variables';
import BackgroundIPC from '@extension/common/background_ipc';
import { SearchAnnotationActions } from '@extension/common/annotation_variables';
import { SocialMediaTypes, SocialMediaAllTypes, SocialMediaActions } from '@extension/common/sma_variables';
import { SETTINGS_TYPE, SCOPE } from '@extension/background/dispatchers/native/native_settings';
import { BrowserType, BuildSettings } from '@extension/common/constants';
import { ContentCommands } from '@extension/common/commands';
import Utils from '@extension/utilities/utils';

/**
 * Wrapper class handling all of the interaction found on the preference tab section in the overall settings page.
 */
export default class Preference
{
  static start(settingsData, bkGlobals, strConvertor)
  {
    // Search annotation settings
    Preference.initSearchAnnotation(settingsData.searchAnnotationOption, bkGlobals);

    // SMA settings
    Preference.initSocialMediaAnnotation(settingsData.sma);

    $('#settings_preference_reset_button').click(() =>
    {
      BackgroundIPC.resetSettings();
    });

    // Handle FF Policy
    FFPolicy.init(settingsData.ffpolicy, bkGlobals);
    // Handle APS Toggle
    APSToggleSetting.init(settingsData.apsToggle, bkGlobals);

    AnnotationJourneyToggleSetting.init(settingsData.ajToast, bkGlobals, strConvertor);
  }

  static initSearchAnnotation(searchAnnotationOption, bkGlobals)
  {
    // For Edge extentions without native, hide 'ONLY_SECURE_SEARCH' option
    if (bkGlobals.nativeSettings.SHOW_SEARCH_SETTINGS.value === false || BrowserType.EDGE === BuildSettings.BROWSER_TYPE)
    {
      $('#settings_row_annotation_ss').hide();
    }

    if (searchAnnotationOption)
    {
      $('input[name=search-annotation]').prop('checked', false);
      $(`input[name=search-annotation][value=${searchAnnotationOption}]`).prop('checked', true);
    }

    $('input[name=search-annotation]').click(({ currentTarget }) =>
    {
      const searchAnnotationOption = $(currentTarget).val();
      BackgroundIPC.searchAnnotation(SearchAnnotationActions.SET, { searchAnnotationOption });
    });
  }

  static initSocialMediaAnnotation(sma)
  {
    const { socialMediaOption, socialMediaToggle } = sma;
    if (!socialMediaToggle) Preference.turnOffSMA(socialMediaOption);
    else Preference.turnOnSMA(socialMediaOption);

    $('.social-media__item__checkbox > input').click(({ currentTarget }) =>
    {
      const socialMediaType = Number($(currentTarget).attr('sma-type'));
      const imgElement = $(`.social-media__item__logo > img[sma-type=${socialMediaType}]`);
      let action = '';
      let src = '';
      if ($(currentTarget).is(':checked'))
      {
        action = SocialMediaActions.ANNOTATION_ENABLE;
        src = imgElement.attr('src-enabled');
      }
      else
      {
        action = SocialMediaActions.ANNOTATION_DISABLE;
        src = imgElement.attr('src-unchecked');
      }

      imgElement.attr('src', src);

      BackgroundIPC.socialMediaAnnotation(action, { socialMediaType });
    });

    $('.sma-switch > input').click(({ currentTarget }) =>
    {
      // Handle toggle switch
      if ($(currentTarget).is(':checked'))
      {
        BackgroundIPC.socialMediaAnnotation(SocialMediaActions.TOGGLE_ON, {}).then(({ socialMediaOption }) =>
        {
          if (socialMediaOption === SocialMediaTypes.NONE)
          {
            // Enable All options when turning toggle on (if none of them have been checked)
            BackgroundIPC.socialMediaAnnotation(SocialMediaActions.ANNOTATION_ENABLE_ALL, {});
            socialMediaOption = SocialMediaAllTypes;
          }

          Preference.turnOnSMA(socialMediaOption);
        });
        return;
      }

      Preference.turnOffSMA();
      BackgroundIPC.socialMediaAnnotation(SocialMediaActions.TOGGLE_OFF, {});
    });
  }

  static turnOffSMA(socialMediaOption = null)
  {
    $('.social-media__item__logo > img').each((index, imgElement) =>
    {
      const disabledSrc = $(imgElement).attr('src-disabled');
      $(imgElement).attr('src', disabledSrc);
    });

    $('.social-media__item__checkbox > input').prop('disabled', true);
    $('.sma-switch > input').prop('checked', false);

    if (socialMediaOption)
    {
      // find out which media types are enabled
      const enabledMediaTypes = Object.values(SocialMediaTypes).filter((mediaType) =>
      {
        const enable = (mediaType & socialMediaOption);
        return enable;
      });

      $('.social-media__item__checkbox > input').each((index, inputElement) =>
      {
        const samType = Number($(inputElement).attr('sma-type'));
        if (enabledMediaTypes.includes(samType)) $(inputElement).prop('checked', true);
        else $(inputElement).prop('checked', false);
      });
    }
  }

  static turnOnSMA(socialMediaOption)
  {
    // Turn media toggle button ON and reset all other media types
    $('.sma-switch > input').prop('checked', true);
    $('.social-media__item__checkbox > input').prop('disabled', false);
    $('.social-media__item__checkbox > input').prop('checked', false);

    // find out which media types are enabled
    const enabledMediaTypes = Object.values(SocialMediaTypes).filter((mediaType) =>
    {
      const enable = (mediaType & socialMediaOption);
      return enable;
    });

    $('.social-media__item__logo > img').each((index, imgElement) =>
    {
      const samType = Number($(imgElement).attr('sma-type'));
      let src = '';
      if (enabledMediaTypes.includes(samType)) src = $(imgElement).attr('src-enabled');
      else src = $(imgElement).attr('src-unchecked');
      $(imgElement).attr('src', src);
    });

    $('.social-media__item__checkbox > input').each((index, inputElement) =>
    {
      const samType = Number($(inputElement).attr('sma-type'));
      if (enabledMediaTypes.includes(samType)) $(inputElement).prop('checked', true);
      else $(inputElement).prop('checked', false);
    });
  }
}

class FFPolicy
{
  // Code used by the Firefox Policy Change
  static init(ffpolicy, bkGlobals)
  {
    if (bkGlobals.browserType !== BrowserType.FIREFOX)
    {
      return;
    }

    $('#ffpolicy-section').css('display', 'block');

    const { personal, activity } = ffpolicy;
    $('#ffpolicy-personal > input').prop('checked', personal);
    $('#ffpolicy-activity > input').prop('checked', activity);

    FFPolicy.createListener();
    FFPolicy.handleMessage();
  }

  static createListener()
  {
    $('#ffpolicy-personal > input').on('click', ({ currentTarget }) =>
    {
      const state = $(currentTarget).is(':checked');
      BackgroundIPC.setFFPolicyCollection({ personal: state });
      BackgroundIPC.broadcastToForeground({ from: 'settings', state });
    });

    $('#ffpolicy-activity > input').on('click', ({ currentTarget }) =>
    {
      const state = $(currentTarget).is(':checked');
      BackgroundIPC.setFFPolicyCollection({ activity: state });
    });
  }

  static handleMessage()
  {
    // This is to handle incoming messages which notifies this page if the
    // personal switch has been enabled/disable on the "ffpolicy landing page", so
    // we can also dynamically update this as well.
    chrome.runtime.onMessage.addListener((request, sender) =>
    {
      if (sender.id !== chrome.runtime.id)
      {
        return;
      }

      const { command, payload } = request;
      if (command === ContentCommands.BROADCAST_TO_FOREGROUND && payload)
      {
        const { from, state } = payload;
        if (from === 'landing')
        {
          $('#ffpolicy-personal > input').prop('checked', state);
        }
      }
    });
  }
}

class APSToggleSetting
{
  static init(storedAPSSetting, bkGlobals)
  {
    const { APS_COHORT } = bkGlobals.nativeSettings;
    const isSupported = Utils.isChromeBrowser();
    const isEN = Utils.isBrowserEN();
    if (APS_COHORT.value === 0 || !isSupported || !isEN)
    {
      return;
    }

    $('#aps-toggle-section').css('display', 'block');

    $('#aps-toggle-label > input').prop('checked', storedAPSSetting);

    APSToggleSetting.createListener(bkGlobals);
  }

  static createListener(bkGlobals)
  {
    $('#aps-toggle-label > input').on('click', ({ currentTarget }) =>
    {
      const state = $(currentTarget).is(':checked');

      const setSettings = {};
      setSettings[LocalStorageVariables.APS_TOGGLE_SETTING.name] = state;
      BackgroundIPC.cacheStore(CacheStoreActions.SET, setSettings);

      // Update toggle state in native settings
      const setNativeSettings = {
        name: bkGlobals.nativeSettings.APS_TOGGLE_SETTING.name,
        value: state,
        scope: SCOPE.SYSTEM,
        type: SETTINGS_TYPE.BOOL
      };

      BackgroundIPC.resetNativeSetting(setNativeSettings);
    });
  }
}

class AnnotationJourneyToggleSetting
{
  static init(ajToast, bkGlobals, strConvertor)
  {
    AnnotationJourneyToggleSetting.handleCohorts(ajToast, bkGlobals, strConvertor, false);
    AnnotationJourneyToggleSetting.handleMessage(bkGlobals, strConvertor);
  }

  static handleCohorts(ajToast, bkGlobals, strConvertor, isNativeMsg)
  {
    const isEN = Utils.isBrowserEN();
    const isSupported = Utils.isChromeBrowser();
    const { ANNOTATION_JOURNEY_COHORT, SECURE_SEARCH_CHOICE } = bkGlobals.nativeSettings;

    if (ajToast === null || !isSupported || !isEN)
    {
      return;
    }

    const ajPreferenceSSWithTip = strConvertor.localeData('settings_preference_annotation_ss_with_tip');
    const ajPreferenceAll = strConvertor.localeData('settings_preference_annotation_journey_all');

    if ([1, 3].includes(ANNOTATION_JOURNEY_COHORT.value))
    {
      if (ajToast || (SECURE_SEARCH_CHOICE.value && isNativeMsg === false))
      {
        $('#settings_row_annotation_all').hide();
        $('#settings_preference_annotation_ss').removeAttr('x-mclocalizedtext');
        $('#settings_preference_annotation_ss').text(ajPreferenceSSWithTip);
        $(`input[name=search-annotation][value=${'ONLY_SECURE_SEARCH'}]`).prop('checked', true);
        AnnotationJourneyToggleSetting.updateAPSSetting(1);
      }
      else
      {
        $('#annotation-section').hide();
        AnnotationJourneyToggleSetting.updateAPSSetting(0);

        $('#aps-toggle-section').hide();
      }
    }
    else if ([2, 4].includes(ANNOTATION_JOURNEY_COHORT.value))
    {
      if (ajToast || (SECURE_SEARCH_CHOICE.value && isNativeMsg === false))
      {
        $('#settings_row_annotation_ss').hide();
        $('#settings_preference_annotation_all').removeAttr('x-mclocalizedtext');
        $('#settings_preference_annotation_all').text(ajPreferenceAll);
        $(`input[name=search-annotation][value=${'ALL'}]`).prop('checked', true);
        AnnotationJourneyToggleSetting.updateAPSSetting(1);
      }
      else
      {
        $('#annotation-section').hide();
        AnnotationJourneyToggleSetting.updateAPSSetting(0);

        $('#aps-toggle-section').hide();
      }
    }
    else
    {
      AnnotationJourneyToggleSetting.updateAPSSetting(null);
    }
  }

  static handleMessage(bkGlobals, strConvertor)
  {
    chrome.runtime.onMessage.addListener((request, sender) =>
    {
      if (sender.id !== chrome.runtime.id)
      {
        return;
      }

      const { command, payload } = request;
      if (command === ContentCommands.TRIGGER_AJ_TOAST && payload !== null && payload !== undefined)
      {
        AnnotationJourneyToggleSetting.handleCohorts(payload, bkGlobals, strConvertor, true);
      }
    });
  }

  static updateAPSSetting(payload)
  {
    const setSettings = {};
    setSettings[LocalStorageVariables.AJ_APS_ENABLE.name] = payload;
    BackgroundIPC.cacheStore(CacheStoreActions.SET, setSettings);
  }
}
