import { extension } from '@extension/common/webextension';
import BackgroundCommon from '@extension/common/background_common';

export default class Statistics
{
  static createStatisticsRow(l10n, image, count, last = false)
  {
    return `
    <div class='statistics__stats-box__item ${last ? 'no-border' : ''}'>
      <img src=${image} />
      <div id='num-pages-scanned' class='statistics__stats-box__item__number'>${count}</div>
      &nbsp;
      <div id=${l10n} x-mclocalizedtext='true'></div>
    </div>`;
  }

  static getStatsToShow(stats)
  {
    const statsData = {
      pagesScanned: {
        l10n: 'settings_statistics_pages_scanned',
        image: 'images/statistics/pages-scanned-icon.svg',
        count: 0,
      },
      downloadsScanned: {
        l10n: 'settings_statistics_downloads_scanned',
        image: 'images/statistics/downloads-scanned-icon.svg',
        count: 0,
      },
      downloadsBlocked: {
        l10n: 'settings_statistics_downloads_blocked',
        image: 'images/statistics/downloads-blocked-icon.svg',
        count: 0,
      },
      pagesBlocked: {
        l10n: 'settings_statistics_pages_blocked',
        image: 'images/statistics/pages-blocked-icon.svg',
        count: 0,
      },
      cryptoBlocked: {
        l10n: 'settings_statistics_crypto_blocked',
        image: 'images/statistics/crypto-blocked-icon.svg',
        count: 0,
      },
    };

    // populate feature stats
    const {
      cryptoBlocked,
      downloadsBlocked,
      downloadsScanned,
      pagesBlocked,
      pagesScanned
    } = stats;

    statsData.pagesScanned.count = BackgroundCommon.getUINumber(pagesScanned);
    statsData.downloadsScanned.count = BackgroundCommon.getUINumber(downloadsScanned);
    statsData.downloadsBlocked.count = BackgroundCommon.getUINumber(downloadsBlocked);
    statsData.pagesBlocked.count = BackgroundCommon.getUINumber(pagesBlocked);
    statsData.cryptoBlocked.count = BackgroundCommon.getUINumber(cryptoBlocked);

    // For now it returns an array of all entries.
    // Can be made to show each row based on feature flags
    return Object.values(statsData);
  }

  static start(settingsData)
  {
    const statsBox = $('.statistics__stats-box');
    const statsToShow = Statistics.getStatsToShow(settingsData.stats);

    for (let index = 0; index < statsToShow.length; index += 1)
    {
      const { l10n, image, count } = statsToShow[index];
      const last = index === statsToShow.length - 1;
      const row = Statistics.createStatisticsRow(l10n, extension.runtime.getURL(image), count, last);
      statsBox.append(row);
    }
  }
}
