import BackgroundIPC from '@extension/common/background_ipc';
import { WhitelistActions, WhitelistTypes } from '@extension/common/whitelist_variables';
import UrlParser from '@extension/common/url_parser';

export default class Whitelist
{
  static start(settingsData)
  {
    const { whitelistedLinks } = settingsData;

    $('#add-btn').click(() =>
    {
      Whitelist.addWhitelistEntryFromInput();
      Whitelist.disableAddButton();
    });

    $('#remove-btn').click(() =>
    {
      const cleanUrls = [];
      $('#whitelist-list').find('li').each((index, element) =>
      {
        if ($(element).hasClass('selected'))
        {
          cleanUrls.push(UrlParser.getCleanURI($(element).text()));
          $(element).remove();
        }
      });

      BackgroundIPC.whitelist(WhitelistActions.REMOVE, WhitelistTypes.BLOCK_PAGE, { domains: cleanUrls });

      Whitelist.disableRemoveButton();
    });

    $('#settings_whitelist_input').focus(() =>
    {
      $('#whitelist-list').find('li.selected').removeClass('selected');
      Whitelist.disableRemoveButton();

      if (Whitelist.isValidUrl($('#settings_whitelist_input').val()))
      {
        Whitelist.enableAddButton();
      }
    });

    $('#settings_whitelist_input').on('input', () =>
    {
      if (Whitelist.isValidUrl($('#settings_whitelist_input').val()))
      {
        Whitelist.enableAddButton();
        return;
      }

      Whitelist.disableAddButton();
    });

    $('#settings_whitelist_input').keydown(({ keyCode }) =>
    {
      if (Whitelist.isValidUrl($('#settings_whitelist_input').val()) && keyCode === 13)
      {
        Whitelist.addWhitelistEntryFromInput();
        Whitelist.disableAddButton();
      }
    });

    // Populate whitelist
    for (const link of whitelistedLinks)
    {
      Whitelist.insertListItemToWhitelist(link);
    }
  }

  static insertListItemToWhitelist(html)
  {
    const whitelistElement = $('#whitelist-list');
    const li = $('<li tabindex="0">');
    li.click(({ target }) =>
    {
      $(target).hasClass('selected') ? $(target).removeClass('selected') : $(target).addClass('selected');
      if (whitelistElement.find('li.selected').length === 0)
      {
        Whitelist.disableRemoveButton();
        return;
      }

      Whitelist.enableRemoveButton();
    });

    li.html(html);
    whitelistElement.append(li);
  }

  static addWhitelistEntryFromInput()
  {
    let urlToAdd = $('#settings_whitelist_input').val();
    urlToAdd = UrlParser.getCleanURI(urlToAdd);

    // Check if url already exists
    for (const item of $('#whitelist-list').find('li'))
    {
      const inputUrl = $(item).text();
      if (inputUrl.includes(urlToAdd) || urlToAdd.includes(inputUrl))
      {
        $('#settings_whitelist_input').val('');
        return;
      }
    }

    Whitelist.insertListItemToWhitelist(urlToAdd);
    BackgroundIPC.whitelist(WhitelistActions.ADD, WhitelistTypes.BLOCK_PAGE, { domains: [urlToAdd] });
    $('#settings_whitelist_input').val('');
  }

  // Check if url is valid to add to whitelist
  static isValidUrl(url)
  {
    const invalidChars = /[^\x2D\x2E\x30-\x39\x41-\x5A\x61-\x7A]/g;
    const invalidDots = /^\.|\.\.|\.$/g;
    const validDot = /\./g;
    const validStart = /^[a-zA-Z0-9]/g;
    const MIN_URL_LENGTH = 5;
    const MAX_URL_LENGTH = 255;
    return (
      url.length !== 0
      && !/^\s+$/g.test(url)
      && url.length >= MIN_URL_LENGTH
      && url.length <= MAX_URL_LENGTH
      && !invalidChars.test(url)
      && !invalidDots.test(url)
      && validDot.test(url)
      && validStart.test(url)
    );
  }

  static enableAddButton()
  {
    $('#add-btn').prop('disabled', false);
  }

  static disableAddButton()
  {
    $('#add-btn').prop('disabled', true);
  }

  static enableRemoveButton()
  {
    $('#remove-btn').prop('disabled', false);
  }

  static disableRemoveButton()
  {
    $('#remove-btn').prop('disabled', true);
  }
}
