import BackgroundIPC from '@extension/common/background_ipc';
import UrlParser from '@extension/common/url_parser';
import { extension } from '@extension/common/webextension';
// Styles
import '@styles/settings.scss';
// Images
import UIStrings from '@extension/utilities/ui_strings';
// Settings Tabs
import About from '@extension/ui/foreground/settings_tab/about';
import Preference from '@extension/ui/foreground/settings_tab/preference';
import Statistics from '@extension/ui/foreground/settings_tab/statistics';
import Whitelist from '@extension/ui/foreground/settings_tab/whitelist';
// Telemetry
import ContextTelemetry from '@extension/telemetry/context_telemetry';
import { FEATURE_TAB_ID, TelemetryTerms, EventNames } from '@extension/telemetry/common_telemetry';
/////////////////////////////////
////////
//////////////////////////////////////////////////////////
//////////

class SettingsUI
{
  constructor()
  {
    // Tab details
    this.featureTabs = {
      whitelist: {
        id: FEATURE_TAB_ID.WHITELIST,
        html: 'html/whitelist.html',
        class: Whitelist,
        active: false,
      },
      preference: {
        id: FEATURE_TAB_ID.PREFERENCE,
        html: 'html/preference.html',
        class: Preference,
        active: true,
      },
      statistics: {
        id: FEATURE_TAB_ID.STATISTICS,
        html: 'html/statistics.html',
        class: Statistics,
        active: true,
      },
      about: {
        id: FEATURE_TAB_ID.ABOUT,
        html: 'html/about.html',
        class: About,
        active: false,
      },
    };

    // Array containing tabs that will be shown
    this.navigationTabs = [];
  }

  // This function is used for deciding which tab to enable/show
  enableNavigationTabs()
  {
    // Note: The order in which they are added are preserved in the UI view
    this.navigationTabs.push(this.featureTabs.whitelist);
    this.navigationTabs.push(this.featureTabs.preference);
    this.navigationTabs.push(this.featureTabs.about);
  }

  // This function will fetch and inject the tab's content (html) into
  // the main_container of the settings.html page. This is based on
  // which tabs were enabled through func enableNavigationTabs()
  popuplateHTML()
  {
    const fetchAndPopulate = (id, html) =>
    {
      const mainContainer = $('#main_container');
      const promise = fetch(extension.runtime.getURL(html)).then((response) => response.text()).then((body) =>
      {
        const content = $(`<div id=${id}></div>`);
        mainContainer.append(content);
        // Cannot use jquery's hml since it adds a
        // script tag which causes CS-Policy issue
        if (document.getElementById(id))
        {
///////////////////////////////////////////
/////////////////////////////////////////////////////////////////////
////////////////////
////////////////////////////////////////////////////////////////////
          document.getElementById(id).innerHTML = body;
////////////////////
        }
      });
      return promise;
    };

    const promises = [];
    this.navigationTabs.forEach(({ id, html }) =>
    {
      promises.push(fetchAndPopulate(id, html));
    });

    return Promise.all(promises);
  }

  // Get Settings details from Background Script
  getBackgroundSettings()
  {
    const promises = [];
    promises.push(BackgroundIPC.getSettingsData());
    promises.push(BackgroundIPC.getBkGlobals());

    return Promise.all(promises);
  }

  // Create the Navigation tabs
  createNavigationTabs()
  {
    const tabContainer = $('.tabs');
    this.navigationTabs.slice().reverse().forEach(({ id }) =>
    {
      const tab = $(`<li id=settings_tab_${id} x-mclocalizedtext='true' tab-type=${id} class='tabs__tab' role='tab' tabindex='0'>`);
      tabContainer.prepend(tab);
    });
  }

  // This function will move the navigation slider to its corresponding tab
  // and mark the element as active to highlight the text
  moveNavigationSlider(id, index)
  {
    $('.tabs__tab').removeClass('active');
    $('.tabs__tab').removeAttr('aria-selected').attr('aria-selected', false);
    $(`.tabs__tab[tab-type='${id}']`).addClass('active').attr('aria-selected', true);

    const tabCount = this.navigationTabs.length;
    const tabSize = index * (100 / tabCount);
    $('.tabs__slider').css('left', `${tabSize}%`);
  }

  // Sets the proper width of the navigation slider and the tab for each enabled feature,
  // moves the slider to the proper location, and enables the corresponding content
  initNavigationSlider()
  {
    const tabCount = this.navigationTabs.length;
    const tabSize = 100 / tabCount;
    $('.tabs__slider').css('width', `${tabSize}%`);
    $('.tabs__tab').css('width', `${tabSize}%`);

    for (let index = 0; index < this.navigationTabs.length; index += 1)
    {
      const { active, id } = this.navigationTabs[index];
      if (active)
      {
        $(`.content.${id}`).show();

        this.moveNavigationSlider(id, index);
        break;
      }
    }
  }

  // Enable Navigation Listener to show the proper tab and its content
  // based on user's tab click
  navigationTabListener()
  {
    const activateTabContent = (tabId) =>
    {
      const tabTypes = this.navigationTabs.map(({ id }) => id);

      if (!tabTypes.includes(tabId)) return;

      // Update Navigation Slider
      for (let index = 0; index < this.navigationTabs.length; index += 1)
      {
        const { id } = this.navigationTabs[index];
        if (id === tabId)
        {
          this.moveNavigationSlider(id, index);
          break;
        }
      }

      // Update Content
      $('.content').hide();
      $(`.content.${tabId}`).show();
    };

    // Show specific tab based on url parameter
    const urlTabType = UrlParser.getParam(window.location.href, 'tabtype');
    if (urlTabType) activateTabContent(urlTabType);

    // Listener for subsequent tab clicks
    const tabs = $('.tabs__tab');
    tabs.click(({ currentTarget }) =>
    {
      const tabId = $(currentTarget).attr('tab-type');
      activateTabContent(tabId);
    });
    tabs.keyup(({ keyCode, currentTarget }) =>
    {
      if (keyCode === 32)
      {
        const tabId = $(currentTarget).attr('tab-type');
        activateTabContent(tabId);
      }
    });
  }

  setupNavigationTabs()
  {
    this.createNavigationTabs();
    this.initNavigationSlider();
    this.navigationTabListener();
  }

  async init()
  {
    // Get Background Settings
    const results = await this.getBackgroundSettings();
    const [settingsData, bkGlobals] = results;

    // Decide which features are enabled and populate html
    this.enableNavigationTabs(bkGlobals);
    await this.popuplateHTML();

    // Setup Settings page Navigation
    this.setupNavigationTabs();

    // Setup UI String localization
    const strConvertor = new UIStrings();

    // Start each class
    this.navigationTabs.forEach((tabDetails) =>
    {
      tabDetails.class.start(settingsData, bkGlobals, strConvertor);
    });

    // Auto populate localization string
    strConvertor.init();

    // Handle Telemetry
    const options = {
      identifier: EventNames.PROFILE_INFO,
      whitelistedKeys: [TelemetryTerms.ACTION.RETURN_KEY_CODE]
    };
    ContextTelemetry.handle(options);
  }
}

$(document).ready(() =>
{
  const settingsUI = new SettingsUI();
  settingsUI.init();
});
